<template>
  <div class="hmw">
    <!--  -->
    <!-- <van-loading color="#1989fa" /> -->
    <van-popup v-model="show">
      <p></p>
      <img src="../../assets/ewm.png" alt="">
    </van-popup>
        <!--  -->
        <van-sticky>
            <div class="hmw-top">
            <!--  -->
      <van-icon name="arrow-left" @click="hmwJumpTo" />
      <p v-show="!hmwFlag"></p>
      <div class="hmwNav" v-show="hmwFlag">
          <span :class="hmwIndex==0?'hmwActiveTop':''" @click="hmwDian(0)" id="hmwKc"></span>
          <span :class="hmwIndex==1?'hmwActiveTop':''" @click="hmwDian(1)" id="hmwDg"></span>
          <span :class="hmwIndex==2?'hmwActiveTop':''" @click="hmwDian(2)" id="hmwPj"></span>
      </div>
      <van-icon name="cluster-o" @click="showPopup" />
    </div>
</van-sticky>
      
    <!--  -->
    <div class="hmw-center" style="background: #f0f2f5;">
      <!--  -->
      <van-list>
        <ol>
          <!--  -->
          <li class="hmwXQ" v-if="hmwObj.teachers_list">
            <p class="hmwP1 hmwTOP1">{{hmwObj.title}}</p>
            <p class="hmwP2">{{hmwObj.price==0?'':hmwObj.price}}</p>
            <p class="hmwP3">{{hmwObj.total_periods}} | {{ hmwObj.brows_num }}</p>
            <p class="hmwP3">{{ hmwObj.start_play_date | timefnxq }} - {{
                    hmwObj.end_play_date | timefnxq
                  }}</p>
                  <!-- <van-rate v-model="hmwSc" :count="1"/> -->
            <van-icon class="hmwNo" v-show="!hmwSc" name="star-o" @click="hmwYes"/>
            <van-icon class="hmwYes" v-show="hmwSc" name="star" @click="hmwNo"/>
          </li>
          <!--  -->
          <li class="hmwTD">
            <p class="hmwP1"></p>
            <div>
              <img :src="hmwObj.teachers_list[0].teacher_avatar" alt="" /><span
                  >{{ hmwObj.teachers_list[0].teacher_name }}</span
                >
            </div>
          </li>
          <!--  -->
          <li class="hmwJS hmwSroll">
            <p class="hmwP1"></p>
          </li>
          <!--  -->
          <li class="hmwDG hmwSroll" style="padding-bottom: 2px;">
            <p class="hmwP1"></p>
            <!--  -->
            <ul>
              <li :key="index" v-for="(item, index) in 10">
                <p>
                  <span class="hmwS1" style=""></span
                  ><span class="hmwS2"></span>
                </p>
                <p class="hmwP3">8 | 134</p>
              </li>
            </ul>
          </li>
          <!--  -->
          <li class="hmwPL hmwSroll">
            <p class="hmwP1"></p>
            <ul>
              <li :key="index" v-for="(item, index) in 10">
                <img :src="hmwObj.teachers_list[0].teacher_avatar" alt="" />
                <div class="hmwPL-center">
                  <p class="hmwCP1">
                    <span>1234566789</span
                    ><van-rate
                      :size="14"
                      void-color="#eee"
                      color="rgb(234, 122, 47)"
                      readonly
                    />
                  </p>
                  <p class="hmwCP2"></p>
                </div>
                <div class="hmwPL-right">2020-07-15 21:32</div>
              </li>
            </ul>
          </li>
        </ol>
      </van-list>
    </div>
    <!--  -->
    <van-tabbar>
  <div class="hmw-foot">
      <van-button v-if="!hmwBtnFlag" type="primary" block @click="hmwStudyJump()"></van-button>
      <van-button v-if="hmwBtnFlag" type="primary" block @click="hmwStudyJump()"></van-button>
    </div>
</van-tabbar>
    
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  //  
  props: {},
  // 
  components: {},
  // 
  data() {
    return {
      //    --
      active: 2,
      //   
      hmwFlag: false,
      // 
      hmwIndex:0,
    //   
      hmwShow:false,
    //   
    hmwObj:JSON.parse(sessionStorage.getItem('hmwXQ')),
    // 
    hmwBtnFlag:false,
    // 
    show: false,
    // 
    hmwSc:false,
    // 
    hmwPathFrom:JSON.parse(sessionStorage.getItem("hmwPath"))
    };
  },
  // 
  computed: {},
  // 
  watch: {

  },
  // 
  methods: {
    //   nav
     scrollHandle: function(e) {
        var top = e.srcElement.scrollingElement.scrollTop;    // 
        if (top > 0) {
         this.hmwFlag=true
        } else{
          this.hmwFlag = false
        }
      },
    //   nav
    hmwDian(index){
        // // 
        this.hmwIndex = index
        console.log(this.hmwIndex)
        //  offsetTop
      // css 1  0  +1
      const targetOffsetTop = document.querySelector(`.van-list>ol>li:nth-child(${index+3})`).offsetTop-50
      //  offsetTop
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      //  50 
      const STEP = 50
      // 
      if (scrollTop > targetOffsetTop) {
        // 
        smoothUp()
      } else {
        // 
        smoothDown()
      }
      // 
      function smoothDown() {
        //  scrollTop  targetOffsetTop 
        if (scrollTop < targetOffsetTop) {
          //  STEP  STEP
          // 
          if (targetOffsetTop - scrollTop >= STEP) {
            scrollTop += STEP
          } else {
            scrollTop = targetOffsetTop
          }
          // 
          
          document.body.scrollTop = scrollTop
          document.documentElement.scrollTop = scrollTop
          //  requestAnimationFrame  setInterval 
          requestAnimationFrame(smoothDown)
        }
      }
      // 
      function smoothUp() {
        if (scrollTop > targetOffsetTop) {
          if (scrollTop - targetOffsetTop >= STEP) {
            scrollTop -= STEP
          } else {
            scrollTop = targetOffsetTop
          }
          document.body.scrollTop = scrollTop
          document.documentElement.scrollTop = scrollTop
          requestAnimationFrame(smoothUp)
        }
      
    }
    },
    // 
    hmwStudyJump(){
        this.$router.push('/study')
         document.documentElement.scrollTop =0
    },
    // 
    showPopup() {
      this.show = true;
    },
    // 
    hmwYes(){
      this.hmwSc = true
      Toast.success('')
    },
    // 
    hmwNo(){
      this.hmwSc = false
      Toast('')
    },
     // 
    onScroll() {
      // 
      const navContents = document.querySelectorAll('.hmwSroll')
      // console.log(navContents)
      //  offsetTop
      const offsetTopArr = []
      navContents.forEach(item => {
        offsetTopArr.push(item.offsetTop)
      })
      //  scrollTop
      const scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      // console.log(scrollTop)
      // 
      let navIndex = 0
      for (let n = 0; n < offsetTopArr.length; n++) {
        //  scrollTop n offsetTop  n-1 
        // n
        if (scrollTop >= offsetTopArr[n]-50) {
          navIndex = n
        }
      }
      // 
      this.hmwIndex = navIndex
      // console.log(this.hmwIndex)
      this.active = navIndex
    },
  // 
  async hnwGetList(){
    console.log(this.hmwObj.teachers_list[0].course_basis_id)
    let {data} = await this.$Net.courseXQList();
    console.log(data)
  },
  // 
  hmwJumpTo(){
    console.log(this.hmwPathFrom)
    this.$router.push({
        path: this.hmwPathFrom.path,
        name: this.hmwPathFrom.name
      });
  }
  },
  mounted() {
      document.documentElement.scrollTop =0
    window.addEventListener('scroll', this.scrollHandle);  // 
     // 
    window.addEventListener('scroll', this.onScroll, false)
    // 
    this.hnwGetList()
  },
  destroy() {
    // vue
    window.removeEventListener('scroll', this.onScroll)
  },
};
</script>
